"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

// Mock data for meter readings
const mockMeterData = {
  meters: [
    {
      type: "Electricity",
      serialNumber: "E123456789",
      mpan: "1234567890123",
      lastReading: 12345,
      lastReadingDate: "2024-01-10",
      estimatedReading: 12450
    },
    {
      type: "Gas",
      serialNumber: "G987654321",
      mprn: "9876543210",
      lastReading: 6789,
      lastReadingDate: "2024-01-10",
      estimatedReading: 6850
    }
  ],
  readingHistory: [
    { date: "2024-01-10", type: "Electricity", reading: 12345, source: "Customer" },
    { date: "2024-01-10", type: "Gas", reading: 6789, source: "Customer" },
    { date: "2023-12-10", type: "Electricity", reading: 12200, source: "Estimated" },
    { date: "2023-12-10", type: "Gas", reading: 6650, source: "Estimated" },
    { date: "2023-11-10", type: "Electricity", reading: 12050, source: "Customer" },
    { date: "2023-11-10", type: "Gas", reading: 6520, source: "Customer" },
  ]
};

export default function MeterReadingPage() {
  const [data, setData] = useState(mockMeterData);
  const [readings, setReadings] = useState({
    electricity: "",
    gas: ""
  });
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [message, setMessage] = useState("");
  const [error, setError] = useState("");

  useEffect(() => {
    // Simulate API call
    setTimeout(() => {
      setLoading(false);
    }, 1000);
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setSubmitting(true);
    setMessage("");
    setError("");

    // Validation
    if (!readings.electricity && !readings.gas) {
      setError("Please enter at least one meter reading.");
      setSubmitting(false);
      return;
    }

    // Validate readings are higher than last readings
    if (readings.electricity && parseInt(readings.electricity) <= data.meters[0].lastReading) {
      setError("Electricity reading must be higher than your last reading.");
      setSubmitting(false);
      return;
    }

    if (readings.gas && parseInt(readings.gas) <= data.meters[1].lastReading) {
      setError("Gas reading must be higher than your last reading.");
      setSubmitting(false);
      return;
    }

    try {
      // Mock API call - in production, this would submit to the backend
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      setMessage("Meter readings submitted successfully! Your next bill will reflect these readings.");
      setReadings({ electricity: "", gas: "" });
      
      // Update the mock data to reflect new readings
      const newData = { ...data };
      if (readings.electricity) {
        newData.meters[0].lastReading = parseInt(readings.electricity);
        newData.meters[0].lastReadingDate = new Date().toISOString().split('T')[0];
      }
      if (readings.gas) {
        newData.meters[1].lastReading = parseInt(readings.gas);
        newData.meters[1].lastReadingDate = new Date().toISOString().split('T')[0];
      }
      setData(newData);
      
    } catch (err) {
      setError("Failed to submit meter readings. Please try again.");
    } finally {
      setSubmitting(false);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-lg">Loading meter information...</div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div>
        <h1 className="text-3xl font-bold text-gray-900">Meter Readings</h1>
        <p className="text-gray-600 mt-2">Submit your latest meter readings to ensure accurate billing</p>
      </div>

      <Tabs defaultValue="submit" className="w-full">
        <TabsList>
          <TabsTrigger value="submit">Submit Reading</TabsTrigger>
          <TabsTrigger value="history">Reading History</TabsTrigger>
          <TabsTrigger value="guide">Reading Guide</TabsTrigger>
        </TabsList>

        <TabsContent value="submit" className="space-y-6">
          {message && (
            <Alert>
              <AlertDescription className="text-green-700">{message}</AlertDescription>
            </Alert>
          )}

          {error && (
            <Alert variant="destructive">
              <AlertDescription>{error}</AlertDescription>
            </Alert>
          )}

          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            <Card>
              <CardHeader>
                <CardTitle>Submit New Readings</CardTitle>
                <CardDescription>Enter your current meter readings below</CardDescription>
              </CardHeader>
              <CardContent>
                <form onSubmit={handleSubmit} className="space-y-6">
                  {data.meters.map((meter, index) => (
                    <div key={meter.type} className="space-y-3">
                      <div className="border rounded-lg p-4">
                        <h3 className="font-semibold text-lg mb-2">{meter.type} Meter</h3>
                        <div className="text-sm text-gray-600 space-y-1 mb-3">
                          <p>Serial Number: {meter.serialNumber}</p>
                          <p>{meter.type === "Electricity" ? "MPAN" : "MPRN"}: {meter.type === "Electricity" ? meter.mpan : meter.mprn}</p>
                          <p>Last Reading: {meter.lastReading} ({meter.lastReadingDate})</p>
                          <p>Estimated Current: {meter.estimatedReading}</p>
                        </div>
                        <div className="space-y-2">
                          <Label htmlFor={meter.type.toLowerCase()}>
                            New {meter.type} Reading
                          </Label>
                          <Input
                            id={meter.type.toLowerCase()}
                            type="number"
                            placeholder={`Enter reading (must be > ${meter.lastReading})`}
                            value={meter.type === "Electricity" ? readings.electricity : readings.gas}
                            onChange={(e) => setReadings({
                              ...readings,
                              [meter.type.toLowerCase()]: e.target.value
                            })}
                            min={meter.lastReading + 1}
                          />
                        </div>
                      </div>
                    </div>
                  ))}

                  <Button type="submit" disabled={submitting} className="w-full">
                    {submitting ? "Submitting..." : "Submit Readings"}
                  </Button>
                </form>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Important Information</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <h4 className="font-semibold mb-2">Before You Submit:</h4>
                  <ul className="text-sm space-y-1 text-gray-600">
                    <li>• Take readings from all your meters on the same day</li>
                    <li>• Make sure readings are higher than your last submission</li>
                    <li>• Double-check all digits before submitting</li>
                    <li>• Submit readings monthly for accurate billing</li>
                  </ul>
                </div>

                <div>
                  <h4 className="font-semibold mb-2">Reading Tips:</h4>
                  <ul className="text-sm space-y-1 text-gray-600">
                    <li>• Read from left to right</li>
                    <li>• Ignore any red digits or digits after a decimal point</li>
                    <li>• If you have a digital meter, note all the digits shown</li>
                    <li>• For dial meters, read each dial individually</li>
                  </ul>
                </div>

                <div className="bg-blue-50 p-4 rounded-lg">
                  <h4 className="font-semibold text-blue-800 mb-2">Need Help?</h4>
                  <p className="text-sm text-blue-700">
                    If you're having trouble reading your meter, check our reading guide or contact our support team.
                  </p>
                </div>
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent value="history">
          <Card>
            <CardHeader>
              <CardTitle>Reading History</CardTitle>
              <CardDescription>Your previous meter readings</CardDescription>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Date</TableHead>
                    <TableHead>Meter Type</TableHead>
                    <TableHead>Reading</TableHead>
                    <TableHead>Source</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {data.readingHistory.map((reading, index) => (
                    <TableRow key={index}>
                      <TableCell>{reading.date}</TableCell>
                      <TableCell>{reading.type}</TableCell>
                      <TableCell className="font-medium">{reading.reading}</TableCell>
                      <TableCell>
                        <span className={`px-2 py-1 rounded-full text-xs ${
                          reading.source === "Customer" 
                            ? "bg-green-100 text-green-800" 
                            : "bg-yellow-100 text-yellow-800"
                        }`}>
                          {reading.source}
                        </span>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="guide">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <Card>
              <CardHeader>
                <CardTitle>Digital Meter</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="bg-gray-100 p-4 rounded-lg text-center">
                  <div className="text-2xl font-mono bg-black text-green-400 p-2 rounded">12345</div>
                  <p className="text-sm mt-2">Read all digits from left to right</p>
                </div>
                <ul className="text-sm space-y-2">
                  <li>• Note down all the digits shown on the display</li>
                  <li>• Ignore any digits after a decimal point</li>
                  <li>• Some meters cycle through different displays - look for kWh</li>
                </ul>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Dial Meter</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="bg-gray-100 p-4 rounded-lg">
                  <p className="text-sm text-center mb-2">Read each dial from left to right</p>
                  <div className="flex justify-center space-x-2">
                    {[1, 2, 3, 4, 5].map((num) => (
                      <div key={num} className="w-8 h-8 border-2 border-gray-400 rounded-full flex items-center justify-center text-xs">
                        {num}
                      </div>
                    ))}
                  </div>
                </div>
                <ul className="text-sm space-y-2">
                  <li>• Read the number the pointer has just passed</li>
                  <li>• If the pointer is between numbers, use the lower number</li>
                  <li>• Ignore any red dials</li>
                </ul>
              </CardContent>
            </Card>
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}
